import React from 'react';
import {Redirect} from 'react-router-dom';
import { Card, Table, Col } from 'react-materialize';
import Moment from 'react-moment';
import UdIcon from './ud-icon.jsx';
import Row from 'react-materialize/lib/Row';

export default class Licensing extends React.Component {

    constructor() {
        super();

        this.state = {
            loading: true
        }
    }

    componentWillMount() {
        UniversalDashboard.get("/license/dashboard", x => {
            this.setState({
                ...x,
                loading: false
            })
        });
    }

    render() {

        if (!UniversalDashboard.adminMode)
        {
            <Redirect to="/" />
        }

        if (this.state.loading) {
            return <div />
        }

        return [
            <h4><UdIcon icon="Key" /> Licensing</h4>,
            <Row>
                <Col s={12} l={6}>
                    <h5>License</h5>
                    <Card className="ud-card">
                        <Table>
                            <thead>
                                <tr>
                                    <th>Number of Dashboards</th>
                                    <th>Expiration</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td>{this.state.license.seatNumber}</td>
                                    <td><Moment>{this.state.license.endDate}</Moment></td>
                                </tr>
                            </tbody>
                        </Table>
                    </Card>
                </Col>
                <Col s={12} l={6}>
                    <h5>Licensed Dashboards</h5>
                    <Card className="ud-card">
                        <Table>
                            <thead>
                                <tr>
                                    <th>Machine</th>
                                    <th>Port</th>
                                </tr>
                            </thead>
                            <tbody>
                                { this.state.dashboards.map(x => {
                                    return (
                                        <tr>
                                            <td>{x.machine}</td>
                                            <td>{x.port}</td>
                                        </tr>
                                    )
                                })}
                            </tbody>
                        </Table>
                    </Card>
                </Col>
            </Row>
        ]
    }
}
