import React from 'react';
import UdIcon from './ud-icon.jsx';
import UdTerminal from './ud-terminal';
import M from 'materialize-css';
import * as clipboard from "clipboard-polyfill"
import {Link} from 'react-router-dom';

export default class UDDesign extends React.Component {

    constructor() {
        super();

        this.state = {
            open : false
        }
    }

    componentDidMount() {
        this.modalInstance = M.Modal.init(this.modal);
        var elems = document.querySelectorAll('.fixed-action-btn');
        this.fabInstance = M.FloatingActionButton.init(elems);
    }

    componentWillUnmount() {
        this.modalInstance.destroy();
        this.statusModalInstance.destroy();
        this.fabInstance.destroy();
    }
    
    toggleTerminal() {
        if (this.state.open) {
            this.modalInstance.close();
        } else {
            this.modalInstance.open();
        }
    }

    getFromLS(key) {
        let ls = {};
        if (global.localStorage) {
            try {
            ls = JSON.parse(global.localStorage.getItem(key)) || {};
            } catch (e) {
            /*Ignore*/
            }
        }
        return ls;
    }

    copyLayout() {

        var layout = this.getFromLS("uddesign" + window.location.pathname);
        
        if (layout == null || layout.lg == null) {
            M.toast({html: "No layout saved. Make sure you are using New-UDGridLayout."});
        } 

        layout.lg.forEach(x => {
            x.static = true
        });

        var stringLayout = JSON.stringify(layout);

        clipboard.writeText(stringLayout).then(x => {
            M.toast({ html: "Layout copied to clipboard!"});
        });
    }

    render() {
        return (
            [
                <div className="fixed-action-btn">
                    <a className="btn-floating btn-large red" title="Admin Tools">
                        <UdIcon icon="Tools" />
                    </a>
                    <ul>
                        <li><Link to="/admin/licensing"><a className="btn-floating" style={{backgroundColor: '#61ab30'}} title="Licensing" href="!#"><UdIcon icon="Key" /></a></Link></li>
                        <li><Link to="/admin/diagnostics"><a className="btn-floating orange" title="Diagnostics" href="!#"><UdIcon icon="ChartLine" /></a></Link></li>                              
                        <li><a className="btn-floating" style={{backgroundColor: '#00ffc3'}} title="Copy Layout" onClick={this.copyLayout.bind(this)}><UdIcon icon="Copy" /></a></li>                              
                        <li><a className="btn-floating blue" onClick={this.toggleTerminal.bind(this)} id='btnDesignTerminal' title="Open Admin Terminal"><UdIcon icon="Terminal" /></a></li>                        
                    </ul>
                </div>,
                <div ref={modal => this.modal = modal} className="modal">
                    <div className="modal-content">
                        <h4><UdIcon icon={'Terminal'}/>  Admin Terminal</h4>
                        <UdTerminal />
                    </div>
                </div>
            ]
        )
    }
}